.counterButton {
    font-size: 5rem;
    background-color: white;
    width: 100px;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
}

.counterButton:hover {
    background-color: rgb(0, 0, 0);
    color: white;
    transition: 250ms;
}